@import url('https://fonts.googleapis.com/css?family=Catamaran:300,500|Lato:700');
*,
*::after,
*::before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    padding: 0;
    margin: 0;
    font-size: 20px;
    color: #2E2E2E;
    font-family: 'Catamaran', sans-serif;
    overflow-x: hidden;
}

ul {
    list-style-type: none;
    padding: 0;
    color: white;
}

iframe {
    border: 0;
}

a {
    text-decoration: none;
    color: inherit;
}

.text-orange {
    color: #f16108;
}

.section-headline {
    font-size: 48px;
    color: #2E2E2E;
    font-family: 'Lato', sans-serif;
    font-weight: 600;
    text-align: center;
}

.section--headline-white {
    color: white;
}

.container {
    max-width: 1300px;
    margin: 0 auto;
}

.welcome {
    background: url("../img/background.jpeg");
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-color: #464646;
    -webkit-box-shadow: 0px 2px 15px 1px rgba(0, 0, 0, 0.67);
            box-shadow: 0px 2px 15px 1px rgba(0, 0, 0, 0.67);
    height: 100vh;
    width: 100%;
    z-index: -5;
}

.welcome--button {
    margin: 0 auto;
    letter-spacing: 10px;
    border: 15px solid white;
    padding: 20px 100px;
    text-align: center;
    color: white;
    font-size: 64px;
    font-weight: 900;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    position: absolute;
}

.welcome--button:hover {
    color: #f16108;
    background-color: white;
}

.header {
    width: 100%;
    position: fixed;
    height: 128px;
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    display: block;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    z-index:2;
}

.header--container {
    height: inherit;
    position: relative;
}

.header--button {
    display: none;
}

.header--logo {
    height: inherit;
    padding: 10px;
    vertical-align: middle;
    margin-left: 20px;
    padding-left: 0;
}

.header--headline {
    font-size: 28px;
    font-weight: 600;
    display: inline-block;
    vertical-align: middle;
}

.header-scrolled {
    background: rgba(0, 0, 0, 0.5);
    height: 100px;
}

.navigation {
    height: inherit;
}

.navigation--list {
    font-size: 32px;
    position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    margin: 0;
    height: inherit;
}

.navigation--list * {
    display: inline;
    height: inherit;
}

.navigation--item {
    padding: 0 24px;
    border-left: 3px solid white;
}

.navigation--item:first-child {
    border: 0;
}

.navigation--item:last-child {
    margin-right: 20px;
    padding-right: 0;
}

.navigation--button {
    border: 0;
    border-bottom: 3px solid transparent;
    background: transparent;
    color: inherit;
    -webkit-transition:border 0.3s;
    -o-transition:border 0.3s;
    transition:border 0.3s;
    padding: 0;
}

.navigation--button:hover {
    border-bottom: 3px solid #f16108;
}

.about {
    text-align: center;
}

.about--text {
    font-size: 22px;
    font-weight: 300;
    margin-left: 20px;
    margin-right: 20px;
}

.textboxes {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.textboxes--box {
    font-weight: 500;
    background-color: #f16108;
    color: white;
    padding: 1em;
    width: 50%;
    -webkit-box-shadow: 0px 6px 71px -14px rgba(0, 0, 0, 0.75);
            box-shadow: 0px 6px 71px -14px rgba(0, 0, 0, 0.75);
    margin-top: 0;
    margin-bottom: 60.800px;
    margin-left: 7%;
    margin-right: 7%;
}

.team {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: #f16108;
    padding-bottom: 40px;
    -webkit-box-shadow: 0px -8px 12px -7px rgba(0, 0, 0, 0.75);
            box-shadow: 0px -8px 12px -7px rgba(0, 0, 0, 0.75);
}

.team--container {
    margin: 0;
}

.cards {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.cards--card {
    padding: 40px 20px;
    margin: 0 40px;
    background-color: white;
    text-align: center;
    max-width: 250px;
    -webkit-box-shadow: 0px 0px 65px -15px rgba(0, 0, 0, 1);
            box-shadow: 0px 0px 65px -15px rgba(0, 0, 0, 1);
}

.cards--card img {
    border-radius: 100%;
}

.cards--card p {
    font-weight: 300;
    color: #969696;
}

.footer {
    color: #a9a9a9;
    background-color: #1E1E1E;
    -webkit-box-shadow: 0px -8px 12px -7px rgba(0, 0, 0, 0.75);
            box-shadow: 0px -8px 12px -7px rgba(0, 0, 0, 0.75);
    padding: 40px 50px;
}

.footer--container::after {
    content: "";
    display: table;
    clear: both;
}

.contact,
.map {
    float: left;
    width: 50%;
}

.contact {
    padding: 0 20px;
    text-align: center;
}

.contact h2 {
    margin: 0;
}

.map {
    display: block;
    height: 250px;
}



