@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?10221428');
  src: url('../font/fontello.eot?10221428#iefix') format('embedded-opentype'),
       url('../font/fontello.woff2?10221428') format('woff2'),
       url('../font/fontello.woff?10221428') format('woff'),
       url('../font/fontello.ttf?10221428') format('truetype'),
       url('../font/fontello.svg?10221428#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
 
 [class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "fontello";
  font-style: normal;
  font-weight: normal;
  speak: none;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: .2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
 
.icon-mail:before { content: '\e810'; } /* '' */
.icon-facebook:before { content: '\e811'; } /* '' */
.icon-menu:before { content: '\e812'; } /* '' */
.icon-cancel:before { content: '\e813'; } /* '' */


