@media screen and (max-width: 700px) {
    .map,
    .contact {
        width: 100%;
    }
}

@media screen and (max-width: 600px) {
    .welcome--button {
        -webkit-box-sizing: content-box;
                box-sizing: content-box;
        padding: 10px 50px;
        letter-spacing: 0;
        font-size: 42px;
    }
    .header--headline {
        margin-left: -150px;
        font-size: 16px;
        color: #f16108;
    }
}

@media screen and (max-width: 840px) {
    .cards {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .cards--card {
        max-width: 500px;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .textboxes {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .textboxes--box {
        width: 90%;
    }
}

@media screen and (max-width: 900px) {
    .icon-close {
        display: none;
    }
    .navigation--list {
        display: none;
    }
    .header {
        position: relative;
    }
    .header--button {
        display: block;
        background: none;
        border: 0;
        position: absolute;
        right: 16px;
        top: 36px;
        font-size: 48px;
        color: white;
        padding: 0;
        cursor: pointer;
    }
    .header .navigation-opened {
        position: fixed;
        height: 100%;
        width: 100%;
        right: 0;
        top: 0;
        background: #383838;
        z-index: 50;
        opacity: 1;
    }
    .navigation-opened .icon-close {
        display: block;
    }
    .navigation-opened .icon-menu {
        display: none;
    }
    .navigation-opened .header--button {
        position: absolute;
        top: 36px;
        -webkit-transform: none;
            -ms-transform: none;
                transform: none;
        height: auto;
    }
    .navigation-opened .navigation--list {
        position: static;
        height: auto;
        -webkit-transform: none;
            -ms-transform: none;
                transform: none;
        display: block;
        height: 100%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .navigation-opened .navigation--item {
        display: block;
        border: 0;
        text-align: center;
        padding: 0;
        margin: 20px 0;
        height: 120px;
        font-size: 42px;
    }
    .navigation-opened .navigation--item:first-child,
    .navigation-opened .navigation--item:last-child {
        margin: 20px 0;
    }
    .navigation-opened .navigation--button {
        border: 0;
        border-bottom: 3px solid white;
        padding: 10px 30px;
    }
    .navigation-opened .navigation--button:focus {
        border-color: #f16108;
    }
}



